.card {
  background: #FFFFFF;
  border-radius: 5px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  display: flex;
  flex-direction: column;
  padding: 1em 0.5em;
  text-align: center;
}
.card__image {
  margin: 0 auto;
  width: 280px;
}
.card__body {
  margin-top: 1em;
  font-size: 1.15em;
}
.card__label {
  margin: 0 auto 0.5em;
  text-align: center;
  width: 80%;
}
.card__cost {
  color: #FF5C00;
  font-weight: bold;
  margin-bottom: 1em;
}
.card__button {
  margin-bottom: 2.5em;
  width: 60%;
}